/* remove annoying stuff */
p, h1, h2{
    line-height: normal;
}
a, a:hover, a:active, a:visited, a:focus {
    text-decoration: none;
    color: inherit;
}

/* for fonts */
@font-face{
    font-family: khyay;
    src: url("/font/Khyay-Regular.ttf");
}
.khyay{
    font-family: khyay;
