/* animations */
@-webkit-keyframes fade-in{
    0%{
        opacity: 0%;
    }
    66%{
        opacity: 0%;
    }
    100%{
        opacity: 100%;
    }
}
@keyframes fade-in{
    0%{
        opacity: 0%;
    }
    66%{
        opacity: 0%;
    }
    100%{
        opacity: 100%;
    }
}
@-webkit-keyframes blink-anim{
    0%{
        border: 3px solid #FF833D;
    }25%{
        border: 3px solid transparent;
    }
    50%{
        border: 3px solid #FF833D;
    }100%{
        border: 3px solid transparent;
    }
}
@keyframes blink-anim{
    0%{
        border: 3px solid #FF833D;
    }25%{
        border: 3px solid transparent;
    }
    50%{
        border: 3px solid #FF833D;
    }100%{
        border: 3px solid transparent;
    }
}

.blink{
    -webkit-animation: blink-anim 1s steps(1) forwards;
            animation: blink-anim 1s steps(1) forwards;
    -webkit-animation-fill-mode: none;
            animation-fill-mode: none;
    /* animation-iteration-count: 2; */
    opacity: 100% !important;
}

