.circle-point{
    height: 4vw;
    width: 4vw;
    background-color: #E0E0E0;
    border-radius: 50%;
    display: inline-block;
    position: relative;
}

.circle-point-inside{
    height: 2.4vw;
    width: 2.4vw;
    background-color: #004E96;
    border-radius: 50%;
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -1.2vw 0 0 -1.2vw;
}
.map-link{
    cursor: pointer;
}
.map-link p{
    white-space: nowrap;
}
.map-link:hover .circle-point-inside{
    background-color: #ED2114;
}
